/**=== script modal-innerHTML.js ===*/
/**======================= */
function modal() {
  const eduImgWrap = document.querySelector('.education__img-wrap'),
    modalOverlay = document.querySelector('.modal-overlay');


  /**===================== */
  function showModal() {
    modalOverlay.classList.add('modal-overlay--show');
  }

  function showModalContent1() {
    modalOverlay.insertAdjacentHTML('afterEnd', "<img class='modal-content' src='img/rastr/img-certificate-1-1080@1x.jpg' loading='lazy' alt='Certificate â1'>");
  }

  function showModalContent2() {
    modalOverlay.insertAdjacentHTML('afterEnd', "<img class='modal-content' src='img/rastr/img-certificate-2-1080@1x.jpg' loading='lazy' alt='Certificate â1'>");
  }

  function closeModal() {
    const modalContent = document.querySelector('.modal-content');
    if (modalContent) {
      modalContent.remove();
      modalOverlay.classList.remove('modal-overlay--show');
    }
  }


  /*=============================*/
  /*ÐÐ¾ÐºÐ°Ð·Ð°ÑÑ Ð¼Ð¾Ð´Ð°Ð»ÑÐ½Ð¾Ðµ Ð¾ÐºÐ½Ð¾:*/
  /*ÑÐ¾Ð±ÑÑÐ¸Ñ Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑ Ð½Ð° img-preview*/
  eduImgWrap.addEventListener('click', function (event) {
    let target = event.target; // Ð³Ð´Ðµ Ð±ÑÐ» ÐºÐ»Ð¸Ðº?

    if (target.classList.contains('education__img--1')) {
      showModal();
      showModalContent1();
    }

    if (target.classList.contains('education__img--2')) {
      showModal();
      showModalContent2();
    }
  });


  /*=============================*/
  /*1-ÐÐ°ÐºÑÑÑÑ Ð¼Ð¾Ð´Ð°Ð»ÑÐ½Ð¾Ðµ Ð¾ÐºÐ½Ð¾:*/
  /*ÑÐ¾Ð±ÑÑÐ¸Ñ Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑ Ð½Ð° Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ*/
  document.addEventListener('click', function (event) {
    let target = event.target; // Ð³Ð´Ðµ Ð±ÑÐ» ÐºÐ»Ð¸Ðº?

    if (target.classList.contains('modal-content')) {
      closeModal();
    }
  });

  /*2-ÐÐ°ÐºÑÑÑÑ Ð¼Ð¾Ð´Ð°Ð»ÑÐ½Ð¾Ðµ Ð¾ÐºÐ½Ð¾:*/
  /*ÑÐ¾Ð±ÑÑÐ¸Ñ Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑ Ð½Ð° Ð·Ð°ÑÐµÐ½ÐµÐ½Ð½ÑÐ¹ ÑÐ¾Ð½*/
  modalOverlay.addEventListener("click", function (event) {
    closeModal();
  });

  /*3-ÐÐ°ÐºÑÑÑÑ Ð¼Ð¾Ð´Ð°Ð»ÑÐ½Ð¾Ðµ Ð¾ÐºÐ½Ð¾:*/
  /*ÑÐ¾Ð±ÑÑÐ¸Ñ Ð¿Ð¾ Ð½Ð°Ð¶Ð°ÑÐ¸Ñ ESC*/
  window.addEventListener("keydown", function (event) {
    if (event.key === "Escape") {
      closeModal();
    } else if (event.keyCode === 27) {
      closeModal();
    }
  });
}

export default modal;
